import PropTypes from 'prop-types';
import TextLogo from '../images/vaderLogo_Vader.png';
import {useRef, useState} from 'react';
import {Link} from 'react-router-dom';


const Header = (props) => {

    const [open, setOpen] = useState(false);

    return (
        <header className="w-full flex justify-center md:justify-between lg:justify-center h-24 p-4 text-white link-white items-center z-50 bg-black">


            <Link to="/">
                <img src={TextLogo} className="absolute -left-4 md:left-0 transform scale-75 lg:scale-100 lg:left-8 top-0 h-20 z-50" alt="text logo" />
            </Link>

            <div onClick={() => setOpen(!open)} className="mobile-toggle md:hidden z-50 transform rotate-90 text-4xl absolute right-8 cursor-pointer">
                III
            </div>

            <div className={`nav-links-menu bg-black md:bg-transparent flex flex-col items-start top-14 md:flex-row absolute md:static p-4 w-full lg:w-1/3 ${open ? "hidden" : ""}`} >
            
                <Link to="/Swap" className="sm:flex md:hidden p-4 md:pl-0 text-white font-bold w-full text-left" onClick={() => setOpen(!open)}>Swap</Link>
                <Link to="/Pools" className="sm:flex md:hidden p-4 text-white font-bold w-full text-left" onClick={() => setOpen(!open)}>Pools</Link>
                <Link to="/USDV" className="sm:flex md:hidden p-4 text-white font-bold w-full text-left" onClick={() => setOpen(!open)}>USDV</Link>
                <Link to="/Staking" className="sm:flex md:hidden p-4 text-white font-bold w-full text-left" onClick={() => setOpen(!open)}>Staking</Link>
                <Link to="/Anchor" className="sm:flex md:hidden p-4 text-white font-bold w-full text-left" onClick={() => setOpen(!open)}>Anchor</Link>
                <Link to="/Lending" className="sm:flex md:hidden p-4 text-white font-bold w-full text-left" onClick={() => setOpen(!open)}>Lending</Link>
                <Link to="/Stats" className="sm:flex md:hidden p-4 pr-0 text-white font-bold w-full text-left" onClick={() => setOpen(!open)}>Stats</Link>

                <Link to="/Swap" className="hidden md:flex p-4 md:pl-0 text-white font-bold w-full text-left">Swap</Link>
                <Link to="/Pools" className="hidden md:flex p-4 text-white font-bold w-full text-left">Pools</Link>
                <Link to="/USDV" className="hidden md:flex p-4 text-white font-bold w-full text-left">USDV</Link>
                <Link to="/Staking" className="hidden md:flex p-4 text-white font-bold w-full text-left">Staking</Link>
                <Link to="/Anchor" className="hidden md:flex p-4 text-white font-bold w-full text-left" >Anchor</Link>
                <Link to="/Lending" className="hidden md:flex p-4 text-white font-bold w-full text-left">Lending</Link>
                <Link to="/Stats" className="hidden md:flex p-4 pr-0 text-white font-bold w-full text-left">Stats</Link>

            </div>   
        </header>
    )
}

Header.defaultProps = {
    title: 'Task Tracker',
}

Header.propTypes = {
    title: PropTypes.string,
}

export default Header
